.video__container {
    width: 100%;
    height: 60vh;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background: white;
}

#video-tracking {
    z-index: -2;
    width: 100%;
    height: 60vh;
    position: absolute;
}

.hidden__canvas {
    /* object-fit: fill; */
    z-index: 3;
    /* display: none; */
    /* position: absolute; */
    width: 100%;
    height: 50vh;
}

.circle {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    position: absolute;
    z-index: 5;
    background: rgb(255, 255, 255);
}

.circle__color {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    z-index: 1;
    background: rgb(255, 255, 255);
}

.circle-container {
    display: flex;
    position: absolute;
    width: 15em;
    z-index: 5;
    height: 15em;
    display: flex;
    align-items: center;
    justify-content: center;
}

.circle__item {
    opacity: 0;
    border-radius: 50%;
    background-color: deepskyblue;
    width: 4em;
    height: 4em;
    position: absolute;
    animation: scaleIn 4s infinite cubic-bezier(.36, .11, .89, .32);
}

@keyframes scaleIn {
    from {
        transform: scale(.01, .01);
        opacity: .5;
    }
    to {
        transform: scale(3.5, 3.5);
        opacity: 0;
    }
}

.hover__item {
    display: flex;
    position: absolute;
    align-items: center;
    justify-content: center;
    z-index: 5;
    background-color: rgba(241, 237, 237, 0.747);
    padding: 10px;
    border-radius: 1rem;
    margin-top: -3em;
}

.marker {
    transform: perspective(40px) rotateX(20deg) rotateZ(-45deg);
    border-radius: 50% 50% 50% 0;
    padding: 1px;
    width: 50px;
    height: 50px;
    background-color: rgba(241, 237, 237, 0.747);
    position: absolute;
}

.hover__item h5 {
    font-size: 1.25em;
}

.bottom__content {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 30vh;
}

.color__list {
    width: 95%;
    height: 11vh;
    display: flex;
    align-items: center;
    padding: 5px 10px;
    overflow-x: scroll;
    border-radius: 20px;
    background: #e8e8e9;
    box-shadow: 10px 10px 30px #c5c5c6, -10px -10px 30px #ffffff;
}

.cirle__color {
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin: 0 0 0 15px;
    background: rgb(179, 63, 9);
}

.cirle__color .icons {
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    background: rgba(0, 0, 0, 0.705);
    color: rgb(255, 255, 255);
    position: absolute;
    z-index: 1;
    border-radius: 50%;
    font-weight: 700;
    font-size: 0.9em;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 4px 9px;
    margin-top: -3px;
    margin-left: 23px;
}

.btn__container {
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    padding: 0 0 20px 10px;
}

.btn__flip {
    width: 75px;
    height: 75px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 5em;
    border-radius: 50%;
}

.btn {
    background: #f0f3f4;
    box-shadow: 8px 8px 16px #cccfcf, -8px -8px 16px #ffffff;
    transition: .5s;
}

.btn:hover {
    transform: scale(1.25);
}

.btn__shot {
    transition: .5s;
    width: 95px;
    height: 95px;
    display: flex;
    transition: .5s;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background: #a30000;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
    color: rgba(255, 255, 255, 0.904);
}

.btn__shot:hover {
    transform: scale(1.09);
    background: #7e0303;
}

#camera {
    width: 50px;
    height: 50px;
}

#icons {
    width: 27px;
    height: 27px;
}